
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, FooterBtnEvent, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { ObsValue } from '@/services/observation_service'
import { HtsReferralAppointmentService } from "@/apps/HTS/services/hts_referral_appointment_service"
import { getFacilities } from '@/utils/HisFormHelpers/LocationFieldOptions'
import dayjs from 'dayjs'
import { toDate } from '@/utils/Strs'

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as any,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsReferralAppointmentService(this.patientID, this.providerID)
          this.fields.push(this.referralField())
          this.fields.push(this.referralLocationField())
          this.fields.push(this.appointmentDataField())
        }
      },
      immediate: true
    }
  },
  methods: {
    async onFinish(_: any, computedData: any) {
      const obs = await this.resolveObs(computedData)
      await this.service.createEncounter()
      await this.service.saveObservationList(obs as ObsValue[])
      this.nextTask()
    },
    referralField(): Field {
      return {
        id: "re_test",
        helpText: 'Referral for Re-Testing',
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded('Referral for Re-Testing', v.value) 
          }
        },
        options: () => {
          return this.mapStrToOptions([
            'Re-Test',
            'Recency testing',
            'None'
          ])
        }
      }
    },
    referralLocationField(): Field {
      return {
        id: 'referral_location',
        helpText: 'Intended site for retesting',
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: ({ label }: Option) => ({
          obs: this.service.buildValueText('Referral location', label)
        }),
        condition: (f: any) => f.re_test.value != 'None',
        options: (_: any, filter='') => getFacilities(filter),
        config: {
          footerBtns: [
            {
              name: "Here",
              slot: "end",
              onClickComponentEvents: {
                setValue: (btnEvent: FooterBtnEvent) => {
                  return btnEvent.btnOutput
                }
              },
              onClick: () => HtsReferralAppointmentService.getLocationName()
            }
          ],
          showKeyboard: true,
          isFilterDataViaApi: true
        }
      }
    },
    appointmentDataField(): Field {
      let nextAppointmentDate = dayjs(this.service.date).add(1, 'day').format('YYYY-MM-DD')
      return {
        id: "set_appointment",
        helpText: "Select Appointment Date for Re-testing",
        type: FieldType.TT_DATE_PICKER,
        init: async () => {
          try {
            const res = await this.service.nextAppointment()
            if (res) nextAppointmentDate = res
          } catch (e) {
            // Returns 404 not appointment date is available
          }
          return true
        },
        isRequired: () => true,
        condition: (f: any) => f.referral_location.value,
        defaultValue: () => nextAppointmentDate,
        finalComputedValue: (d: Option) => {
          return {
            obs: this.service.buildValueDate('Appointment date', d.value)
          }
        },
        config: {
          minDate: () => this.service.date,
          infoItems: (date: string) => {
            return [
              { 
                label: 'User set appointment',
                value: toDate(date)
              }
            ]
          },
          hiddenFooterBtns: [
            'Clear'
          ]
        }
      }
    }
  }
})
